<template>
  <div id="set">
    <div>
    <header></header>
      <div class="wrapper" id="pwdWrap">
        <p class="title"><span class="title-icon"></span> </p>
        <el-form ref="form" :model="modifyPwd" label-width="80px">
          <el-form-item label="">
            <el-input v-model="modifyPwd.userName" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="modifyPwd.nickName"></el-input>
            <span>1-20</span>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="modifyPwd.originalPwd"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="modifyPwd.newPwd"></el-input>
          </el-form-item>
          <el-button type="primary" @click="saveUserInfo"></el-button>
        </el-form>
      </div>

      <div class="wrapper" id="sysWrap">
        <p class="title"><span class="title-icon"></span> </p>
        <p class="desc-info">
          <span class="desc-text"></span>
          <el-select v-model="setInfo.noticeTime"  class="small-select" placeholder="">
            <el-option v-for="(item,index) in time" :key="index"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
          <span class="unit"></span>
        </p>
        <p class="desc-info">
          <span class="desc-text"></span>
          <el-select v-model="setInfo.secNoticeTime" class="small-select" placeholder="">
            <el-option v-for="(item,index) in time" :key="index"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
          <span class="unit"></span>
        </p>
        <p class="desc-info">
          <span class="desc-text"></span>
          <el-select v-model="setInfo.noticeWay" class="large-select" placeholder="">
            <el-option v-for="(item,index) in noticeWays" :key="index"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
        </p>
        <p class="desc-info">
          <span class="desc-text"></span>
          <span class="yellow fn-right">[  ] </span>
        </p>
        <!--<el-input id="templateWrapper" type="textarea"-->
          <!--:rows="2"-->
          <!--:autosize="{ minRows: 2, maxRows: 100}"-->
          <!--v-model="setInfo.template">-->
        <!--</el-input>-->
        <el-input id="templateWrapper" v-model="setInfo.template" ></el-input>
        <!--<div id="templateWrapper" v-model="setInfo.template">-->
        </div>
        <el-button type="primary" @click="saveSettingInfo"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import  '../assets/css/set.less'
  import axios from 'axios';
  import ElButton from "../../node_modules/element-ui/packages/button/src/button.vue";

  export default {
    name:'Set',
    data() {
      return {
        modifyPwd:{
          userName:'',
          nickName:'',
          originalPwd:'',
          newPwd:''
        },
        setInfo:{
          noticeTime:'15',
          secNoticeTime:'15',
          noticeWay:'',
          template:'<p>[] </p>\n' +
          '          <p class="indent"><span class="yellow">[]</span> - <span class="yellow">[]</span></p>\n' +
          '          <p class="indent"> <span class="yellow">[]</span></p>\n' +
          '          <p class="indent"> <span class="yellow">[]</span></p>\n' +
          '          <p class="indent"> <span class="yellow">[]</span></p>'
        },
        noticeWays:[
          {
          value: 1,
          label: ''
          },{
            value: 2,
            label: ''
          },{
            value: 3,
            label: ''
          }
      ],
        time:[]
      }
    },
    methods: {
      timeList(){
        let _this=this;
        for(let i=1;i<25;i++){
          let time=5*i;
          _this.time.push(
            {
              value:time,
              label:time
            }
          )
        }
      },
      getUserInfo(){
        let _this=this;
        axios.get(`http://videoonline.zhaopin.com/setting/getuserinfo`).then((res) => {

            if (res.status !== 200) {
              return;
            }
          let resData=res.data;
            if(resData.returncode===0){
              let result=resData.result;
              _this.modifyPwd.userName=result.UserName;
              _this.modifyPwd.nickName=result.NickName;
            }
        })
      },
      getSettingInfo(){
        let _this=this;
        axios.get(`http://videoonline.zhaopin.com/setting/getsettinginfo`).then((res) => {
          if (res.status !== 200) {
            return;
          }
          let resData=res.data;
          if(resData.returncode===0){
            let result=resData.result;
            _this.setInfo.noticeTime=result.NotifyMeTime;
            _this.setInfo.secNoticeTime=result.SecondConfirmTime;
            _this.setInfo.noticeWay=result.NotifyType;
            _this.setInfo.template=result.NotifyTemplete;
          }
        })
      },
      saveUserInfo(){
        let _this=this;
        axios.get(`http://videoonline.zhaopin.com/setting/saveuserinfo`,{
          param:{
            username: _this.setInfo.userName,
            nickname: _this.setInfo.nickName,
            password: _this.setInfo.originalPwd,
            newpwd: _this.setInfo.newPwd
          }
        }).then((res) => {
          console.log(res);
          debugger;
          if (res.status !== 200) {
            return;
          }
          let resData=res.data;
          if(resData.returncode===0){

          }
        })
      },
      saveSettingInfo(){
        let _this=this;
        axios.get(`http://videoonline.zhaopin.com/setting/savesettinginfo`,{
          param:{
            NotifyMeTime: _this.modifyPwd.noticeTime,
            SecondConfirmTime: _this.modifyPwd.secNoticeTime,
            NotifyType: _this.modifyPwd.noticeWay,
            NotifyTemplete: _this.modifyPwd.template
          }
        }).then((res) => {
          if (res.status !== 200) {
            return;
          }
          let resData=res.data;
          if(resData.returncode===0){

          }
        })
      }
    },
    created(){
      this.timeList();
      this.getUserInfo();
      this.getSettingInfo();
    },
    components: {
      ElButton
//      'navigation': Navigation
    }
  }
</script>



